import React, { useState, useEffect } from "react";
import face from "../images/face.svg";
import SignUpForm from "./SignUpForm";
import Description from "./Description";
import arrow from "../images/arrow.svg";

const Profile = () => {
  const [showComponent, setShowComponent] = useState(false, 0);
  const [angle, setAngle] = useState(0);
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
    const handleResize = () => {
      setWidth(window.innerWidth);
    };

    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const toggleDisplay = () => {
    setShowComponent(!showComponent);

    if (width < 600 && showComponent) {
      setAngle(angle + 90);
    } else {
      setAngle(angle + 180);
    }
  };

  return (
    <div className="container">
      <div className="profile">
        <div className="profile-content">
          <img src={face} className="profile-img" alt="Profile" />
          <div className="descript">
            <h2>Front-end challenge!</h2>
            <p>This is a design that you will need to code up and imoress us</p>
          </div>
          <button className="toggleButton" onClick={toggleDisplay}>
            <img
              style={{
                transform: `rotate(${angle}deg)`,
                transition: "transform 1s"
              }}
              src={arrow}
              className="toggleIcon"
              alt="Toggle"
            />
          </button>
        </div>
      </div>
      <div>{showComponent ? <SignUpForm /> : <Description />}</div>
    </div>
  );
};

export default Profile;
